:local .footer {
  height: 78px;
  background: #2f2f36;
  font-size: 13px; }
  @media only screen and (max-width: 900px) {
    :local .footer {
      height: auto; } }
  :local .footer :global .section-center {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    padding: 20px 10px 0px 10px; }
    @media only screen and (max-width: 900px) {
      :local .footer :global .section-center {
        flex-direction: column; } }
    :local .footer :global .section-center ul {
      width: 300px; }
    :local .footer :global .section-center div {
      display: grid;
      grid-template-columns: 50px 1fr;
      max-width: 450px;
      margin-top: -10px; }
      :local .footer :global .section-center div span {
        display: inline-block;
        background: #55555e;
        color: #b1b1b5;
        width: 36px;
        height: 36px;
        text-align: center;
        line-height: 36px;
        border-radius: 50%;
        margin-top: 12px; }
      :local .footer :global .section-center div p {
        display: inline-block; }
    :local .footer :global .section-center img {
      width: 32px;
      height: 32px; }
