@font-face {
  font-family: "Ibmplexsans";
  src: url("./fonts/Ibmplexsans.woff2") format("woff2"), url("./fonts/Ibmplexsans.woff") format("woff"), url("./fonts/Ibmplexsans.ttf") format("truetype"); }

@font-face {
  font-family: "Ibmplexsansbold";
  src: url("./fonts/Ibmplexsansbold.woff2") format("woff2"), url("./fonts/Ibmplexsansbold.woff") format("woff"), url("./fonts/Ibmplexsansbold.ttf") format("truetype"); }

@font-face {
  font-family: "Ibmplexsanslight";
  src: url("./fonts/Ibmplexsanslight.woff2") format("woff2"), url("./fonts/Ibmplexsanslight.woff") format("woff"), url("./fonts/Ibmplexsanslight.ttf") format("truetype"); }

@font-face {
  font-family: "Ibmplexsansextralight";
  src: url("./fonts/Ibmplexsansextralight.woff2") format("woff2"), url("./fonts/Ibmplexsansextralight.woff") format("woff"), url("./fonts/Ibmplexsansextralight.ttf") format("truetype"); }

@font-face {
  font-family: "Ibmplexsansmedium";
  src: url("./fonts/Ibmplexsansmedium.woff2") format("woff2"), url("./fonts/Ibmplexsansmedium.woff") format("woff"), url("./fonts/Ibmplexsansmedium.ttf") format("truetype"); }

body {
  font-family: 'Ibmplexsanslight', 'Tahoma';
  background: #eeeeeb;
  color: #5b5b6c;
  font-size: 19px;
  line-height: 1.37; }

.btn {
  border-radius: 3px;
  background-color: #ffffff;
  width: auto;
  min-width: 180px;
  text-align: center;
  padding: 15px 10px;
  display: inline-block;
  cursor: pointer;
  transition: all .5s; }
  .btn:hover {
    background: rgba(187, 187, 192, 0.3);
    color: #ff644b; }

.section-center {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px 0px;
  clear: both; }
  @media only screen and (max-width: 1200px) {
    .section-center {
      padding: 20px; } }
  .section-center--content {
    max-width: 680px; }

.show-all-screen {
  display: flex;
  text-align: center;
  min-height: calc(100vh - 161px); }
  .show-all-screen .section-center {
    display: flex;
    flex-direction: column;
    align-self: center; }

.content {
  background: white; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Ibmplexsansmedium', 'Tahoma';
  font-weight: 100; }

h2 {
  font-size: 32px; }

h4 {
  margin-bottom: 10px; }

.orange {
  color: #ff644b; }

ul {
  list-style: none;
  margin: 0px;
  padding: 0px; }
  ul li {
    margin-bottom: 10px; }

strong {
  font-family: 'Ibmplexsansmedium', 'Tahoma'; }

a {
  color: rgba(91, 91, 108, 0.8); }
  a:hover {
    color: #5b5b6c; }
