:local .banner {
  min-height: 400px;
  margin: 0 auto;
  background: black url(./img/banner.jpg) no-repeat right bottom;
  padding: 0px;
  overflow: hidden;
  display: flex; }
  @media only screen and (max-width: 1200px) {
    :local .banner {
      height: auto; } }
  @media only screen and (max-width: 450px) {
    :local .banner {
      background: black; } }

:local .content {
  max-width: 621px;
  margin: auto auto auto 90px; }
  @media only screen and (max-width: 1200px) {
    :local .content {
      margin: 20px; } }
  @media only screen and (max-width: 900px) {
    :local .content {
      max-width: calc(100% - 250px); } }
  @media only screen and (max-width: 450px) {
    :local .content {
      max-width: 100%; } }
  :local .content h1 {
    color: #FFF;
    font-size: 40px; }
    @media only screen and (max-width: 680px) {
      :local .content h1 {
        font-size: 28px;
        line-height: 1.3em; } }
  :local .content p {
    color: #bbbbc0;
    font-size: 26px; }
    @media only screen and (max-width: 680px) {
      :local .content p {
        font-size: 18px;
        line-height: 1.2em; } }
