:local .blackBlock {
  background: #222228;
  color: #FFF; }
  :local .blackBlock h2 {
    text-align: center;
    color: #FFF; }
  :local .blackBlock p {
    font-size: 14px;
    line-height: 1.86;
    letter-spacing: normal;
    font-family: 'Ibmplexsansextralight';
    opacity: .75; }
  :local .blackBlock .btnList {
    display: flex;
    justify-content: center;
    margin-top: 30px; }
    @media only screen and (max-width: 680px) {
      :local .blackBlock .btnList {
        flex-direction: column;
        align-items: center; } }
    :local .blackBlock .btnList div {
      margin: 10px; }
      @media only screen and (max-width: 680px) {
        :local .blackBlock .btnList div {
          margin: 10px 0px 0px 0px; } }
  :local .blackBlock .license {
    display: flex;
    max-width: 450px;
    margin: 20px auto 0px auto; }
    :local .blackBlock .license .checkbox {
      margin-top: 5px; }
    :local .blackBlock .license a {
      color: #FFF;
      opacity: .8; }
      :local .blackBlock .license a:hover {
        opacity: 1; }
    :local .blackBlock .license p {
      margin: 0px;
      margin-left: 20px; }
