:local .btn {
  width: 185px;
  height: 48px;
  color: white;
  text-align: center;
  font-size: 16px;
  border-radius: 3px;
  line-height: 48px;
  cursor: pointer;
  opacity: .9;
  transition: all .5s; }
  :local .btn:hover {
    opacity: 1; }
  :local .btn i {
    display: inline-block;
    font-size: 20px;
    margin-right: 10px; }
  :local .btn.vk {
    background: #477499;
    border: none;
    margin-top: 10px; }
  :local .btn.fb {
    background: #365b98; }
  :local .btn.google {
    background: #db4437;
    border: none;
    margin-top: 10px; }
  :local .btn.isDisable {
    opacity: .4; }
